import './style/main.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import { BrowserRouter as Router, Route, Link, Switch } from 'react-router-dom'
import { Navbar, Nav } from 'react-bootstrap'
//回到頂層
import ScrollToTop from './components/ScrollToTop'
//元件
import About from './pages/About'
import Home from './pages/Home'
import Member from './pages/Member'
import Product from './pages/Product'

import TagManager from 'react-gtm-module' ///GTM
import ReactGA from 'react-ga4' ///GA4
import ReactGABasic from 'react-ga' ///GA 通用版

///GA4 設定
ReactGA.initialize('G-E4QQ1VB7BW')
ReactGA.send('pageview')
///GA 通用版 設定
ReactGABasic.initialize('UA-214437997-1')
ReactGABasic.pageview(window.location.pathname + window.location.search)
///GTM 設定
const tagManagerArgs = {
  gtmId: 'GTM-TXJ9XZ2',
}
TagManager.initialize(tagManagerArgs)

function App() {
  console.log(window.location.pathname, window.location.search)
  return (
    <Router basename={process.env.PUBLIC_URL}>
      <>
        <Navbar bg="light" expand="lg">
          <Navbar.Brand>
            <Link to="/">首頁</Link>
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="mr-auto">
              <Link to="/about">關於我們</Link>
              <Link to="/product">產品頁面</Link>
              <Link to="/member">會員頁面</Link>
            </Nav>
          </Navbar.Collapse>
        </Navbar>

        <ScrollToTop>
          <Switch>
            <Route path="/product" component={Product}></Route>
            <Route path="/member" component={Member}></Route>
            <Route path="/about" component={About}></Route>
            <Route path="/">
              <Home />
            </Route>
          </Switch>
        </ScrollToTop>
      </>
    </Router>
  )
}

export default App
